.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.content {
  position: fixed;
  background: white;
  width: 306px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.double {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.content p {
  display: flex;
  padding-left: 3%;
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

p.left-text {
  text-align: left;
}